*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.App{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(./imagenes_app/ilustracion-atardecer-en-el-bosque_1920x1080_xtrafondos.com.jpg);
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;

}
  


.contenedor__total{
  /* border-style: solid; */
  width: 90%;
  margin: 2.5rem;
  border-radius: .5rem;
  background-color:rgba(0, 0, 139, 0.489);
  box-shadow: 0 0 1.5rem black;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  padding: 1rem;
}

/* ----------------------------------------------------- */
/* section 1 y 2 */
.section_main{
  display: flex;
}
.section1{
  width: 50%;
  height: 100%;
  background-color:rgba(255, 140, 0, 0.503);
  margin: .5rem;
  border-radius: .5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.section2{
  width: 50%;
  height: 100%;
  background-color: rgba(0, 128, 0, 0.511);
  margin: .5rem;
  border-radius: .5rem;
  
}

.section1__imagen{
  
  width: 100%;
  border-radius:.5rem .5rem 0 0 ;
  -webkit-border-radius:.5rem .5rem 0 0 ;
  -moz-border-radius:.5rem .5rem 0 0 ;
  -ms-border-radius:.5rem .5rem 0 0 ;
  -o-border-radius:.5rem .5rem 0 0 ;
}

.section1__p{
  color: #1b1b32;
  padding: .2rem;
  font-size: 1.1rem;
  font-weight: 900;
  margin: .5rem;
}



.section2__video{
  width: 100%;
  border-radius:.5rem .5rem 0 0 ;
  /* border-style: solid; */
}
.section2__p{
  color: #1b1b32;
  padding: .2rem;
  font-size: 1.1rem;
  font-weight: 900;
  margin: .5rem;
}

.tom__raider{
  width: 100%;
  height: 22rem;
  border-radius:.5rem .5rem 0 0 ;
  border-style: solid;
  border-color:rgb(0, 0, 0);
  
  
}






@media screen and (max-width:1440px) {
  .App{
    width: 100%;
    height: auto;
    
  }
    
  .contenedor__total{
    width: 100%;
    /* height: 90vh; */
    margin: 1rem;
  }
  .contenedor__total{
   flex-direction: column;
  }
  .section1{
    width: 100%;
    /* height: 100%; */
  }
  .section2{
    width: 100%;
    /* height: 100%; */
  }
  .section1__p{
    text-align:justify;
  }
  .section_main{
    flex-direction: column;
  }

  
}